___CSS_LOADER_EXPORT___.push([module.id, `/* Базовые стили для всех кнопок */
button {
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, border-color 0.2s ease-in-out, opacity 0.2s ease-in-out;
  font-family: sans-serif; /* Добавим шрифт для единообразия */
  line-height: 1; /* Для лучшего выравнивания текста */
  margin: 5px; /* Добавим небольшой отступ между кнопками в Storybook */
}

button:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

/* Стили для варианта 'filled' */
.button--filled {
  background-color: #007bff; /* Синий цвет */
  color: white;
}

.button--filled:not(:disabled):hover {
  background-color: #0056b3; /* Темнее при наведении */
}

/* Стили для варианта 'outline' */
.button--outline {
  background-color: transparent;
  color: #007bff;
  border: 1px solid #007bff;
}

.button--outline:not(:disabled):hover {
  background-color: rgba(0, 123, 255, 0.1); /* Легкий фон при наведении */
}

/* Стили для варианта 'text' */
.button--text {
  background-color: transparent;
  color: #007bff;
  border: none;
}

.button--text:not(:disabled):hover {
  background-color: rgba(0, 123, 255, 0.1); /* Легкий фон при наведении */
}

/* Стили для отключенных вариантов outline и text */
.button--outline:disabled,
.button--text:disabled {
  color: #6c757d; /* Серый цвет для текста */
  opacity: 0.65; /* Стандартная прозрачность для disabled */
}
.button--outline:disabled {
    border-color: #6c757d; /* Серая граница для disabled outline */
} `, "",{"version":3,"sources":["webpack://./src/components/Button/Button.css"],"names":[],"mappings":"AAAA,kCAAkC;AAClC;EACE,kBAAkB;EAClB,YAAY;EACZ,kBAAkB;EAClB,eAAe;EACf,eAAe;EACf,8HAA8H;EAC9H,uBAAuB,EAAE,mCAAmC;EAC5D,cAAc,EAAE,oCAAoC;EACpD,WAAW,EAAE,wDAAwD;AACvE;;AAEA;EACE,mBAAmB;EACnB,YAAY;AACd;;AAEA,gCAAgC;AAChC;EACE,yBAAyB,EAAE,eAAe;EAC1C,YAAY;AACd;;AAEA;EACE,yBAAyB,EAAE,yBAAyB;AACtD;;AAEA,iCAAiC;AACjC;EACE,6BAA6B;EAC7B,cAAc;EACd,yBAAyB;AAC3B;;AAEA;EACE,wCAAwC,EAAE,6BAA6B;AACzE;;AAEA,8BAA8B;AAC9B;EACE,6BAA6B;EAC7B,cAAc;EACd,YAAY;AACd;;AAEA;EACE,wCAAwC,EAAE,6BAA6B;AACzE;;AAEA,mDAAmD;AACnD;;EAEE,cAAc,EAAE,0BAA0B;EAC1C,aAAa,EAAE,0CAA0C;AAC3D;AACA;IACI,qBAAqB,EAAE,uCAAuC;AAClE","sourcesContent":["/* Базовые стили для всех кнопок */\nbutton {\n  padding: 10px 15px;\n  border: none;\n  border-radius: 4px;\n  cursor: pointer;\n  font-size: 1rem;\n  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, border-color 0.2s ease-in-out, opacity 0.2s ease-in-out;\n  font-family: sans-serif; /* Добавим шрифт для единообразия */\n  line-height: 1; /* Для лучшего выравнивания текста */\n  margin: 5px; /* Добавим небольшой отступ между кнопками в Storybook */\n}\n\nbutton:disabled {\n  cursor: not-allowed;\n  opacity: 0.6;\n}\n\n/* Стили для варианта 'filled' */\n.button--filled {\n  background-color: #007bff; /* Синий цвет */\n  color: white;\n}\n\n.button--filled:not(:disabled):hover {\n  background-color: #0056b3; /* Темнее при наведении */\n}\n\n/* Стили для варианта 'outline' */\n.button--outline {\n  background-color: transparent;\n  color: #007bff;\n  border: 1px solid #007bff;\n}\n\n.button--outline:not(:disabled):hover {\n  background-color: rgba(0, 123, 255, 0.1); /* Легкий фон при наведении */\n}\n\n/* Стили для варианта 'text' */\n.button--text {\n  background-color: transparent;\n  color: #007bff;\n  border: none;\n}\n\n.button--text:not(:disabled):hover {\n  background-color: rgba(0, 123, 255, 0.1); /* Легкий фон при наведении */\n}\n\n/* Стили д