___CSS_LOADER_EXPORT___.push([module.id, `/* Стили для оверлея (фона) */
.popup-overlay {
  position: fixed; /* Фиксированное позиционирование */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5); /* Полупрозрачный черный фон */
  display: flex;
  align-items: center; /* Центрирование по вертикали */
  justify-content: center; /* Центрирование по горизонтали */
  z-index: 1000; /* Поверх других элементов */
}

/* Стили для контейнера контента попапа */
.popup-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  position: relative; /* Для позиционирования кнопки закрытия */
  min-width: 300px; /* Минимальная ширина */
  max-width: 80%; /* Максимальная ширина */
}

/* Стили для кнопки закрытия */
.popup-close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 1.5rem;
  line-height: 1;
  cursor: pointer;
  color: #aaa;
}

.popup-close-button:hover {
    color: #333;
}

/* Стили для заголовка */
.popup-title {
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 1.25rem;
  color: #333;
}

/* Стили для тела попапа (контента) */
.popup-body {
  margin-top: 10px;
} `, "",{"version":3,"sources":["webpack://./src/components/PopUp/PopUp.css"],"names":[],"mappings":"AAAA,6BAA6B;AAC7B;EACE,eAAe,EAAE,mCAAmC;EACpD,MAAM;EACN,OAAO;EACP,QAAQ;EACR,SAAS;EACT,oCAAoC,EAAE,8BAA8B;EACpE,aAAa;EACb,mBAAmB,EAAE,+BAA+B;EACpD,uBAAuB,EAAE,iCAAiC;EAC1D,aAAa,EAAE,4BAA4B;AAC7C;;AAEA,yCAAyC;AACzC;EACE,uBAAuB;EACvB,aAAa;EACb,kBAAkB;EAClB,yCAAyC;EACzC,kBAAkB,EAAE,yCAAyC;EAC7D,gBAAgB,EAAE,uBAAuB;EACzC,cAAc,EAAE,wBAAwB;AAC1C;;AAEA,8BAA8B;AAC9B;EACE,kBAAkB;EAClB,SAAS;EACT,WAAW;EACX,gBAAgB;EAChB,YAAY;EACZ,iBAAiB;EACjB,cAAc;EACd,eAAe;EACf,WAAW;AACb;;AAEA;IACI,WAAW;AACf;;AAEA,wBAAwB;AACxB;EACE,aAAa;EACb,mBAAmB;EACnB,kBAAkB;EAClB,WAAW;AACb;;AAEA,qCAAqC;AACrC;EACE,gBAAgB;AAClB","sourcesContent":["/* Стили для оверлея (фона) */\n.popup-overlay {\n  position: fixed; /* Фиксированное позиционирование */\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: rgba(0, 0, 0, 0.5); /* Полупрозрачный черный фон */\n  display: flex;\n  align-items: center; /* Центрирование по вертикали */\n  justify-content: center; /* Центрирование по горизонтали */\n  z-index: 1000; /* Поверх других элементов */\n}\n\n/* Стили для контейнера контента попапа */\n.popup-content {\n  background-color: white;\n  padding: 20px;\n  border-radius: 8px;\n  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);\n  position: relative; /* Для позиционирования кнопки закрытия */\n  min-width: 300px; /* Минимальная ширина */\n  max-width: 80%; /* Максимальная ширина */\n}\n\n/* Стили для кнопки закрытия */\n.popup-close-button {\n  position: absolute;\n  top: 10px;\n  right: 10px;\n  background: none;\n  border: none;\n  font-size: 1.5rem;\n  line-height: 1;\n  cursor: pointer;\n  color: #aaa;\n}\n\n.popup-close-button:hover {\n    color: #333;\n}\n\n/* Стили для заголовка */\n.popup-title {\n  margin-top: 0;\n  margin-bottom: 15px;\n  font-size: 1.25rem;\n  color: #333;\n}\n\n/* Стили для тела попапа (контента) */\n.popup-body {\n  margin-top: 10px;\n} "],"sourceRoot":""}]);
