import React, { useState } from 'react';
import type { Meta, StoryObj } from '@storybook/react';
import PopUp from './PopUp';
import Button from '../Button/Button'; // Импортируем кнопку для открытия попапа

const meta: Meta<typeof PopUp> = {
  title: 'Components/PopUp',
  component: PopUp,
  parameters: {
    layout: 'centered', // Центрируем кнопку, которая открывает попап
  },
  tags: ['autodocs'],
  argTypes: {
    // isOpen и onClose управляются состоянием внутри истории, не через Controls
    isOpen: { control: false },
    onClose: { control: false },
    children: { control: 'text' }, // Позволим менять текст внутри попапа
    title: { control: 'text' }, // Позволим менять заголовок
  },
};

export default meta;

// Типизация истории
type Story = StoryObj<typeof PopUp>;

// Компонент-обертка для управления состоянием попапа
const PopUpWithState: React.FC<Omit<React.ComponentProps<typeof PopUp>, 'isOpen' | 'onClose'>> = (args) => {
  const [isOpen, setIsOpen] = useState(false);

  const handleOpen = () => setIsOpen(true);
  const handleClose = () => setIsOpen(false);

  return (
    <>
      {/* Кнопка для открытия попапа */}
      <Button onClick={handleOpen}>Открыть PopUp</Button>

      {/* Сам PopUp */}
      <PopUp {...args} isOpen={isOpen} onClose={handleClose} />
    </>
  );
};


// Базовая история
export const Default: Story = {
  // Мы рендерим обертку, которая содержит логику открытия/закрытия
  render: (args) => <PopUpWithState {...args} />,
  args: {
    // Аргументы по умолчанию для попапа
    title: 'Заголовок PopUp',
    children: 'Это содержимое модального окна PopUp. Вы можете закрыть его, нажав на крестик, клавишу Esc или кликнув вне окна.',
  },
};

// История без заголовка
export const WithoutTitle: Story = {
  render: (args) => <PopUpWithState {...args} />,
  args: {
    // title не указан
    children: 'PopUp без заголовка.',
  },
};

// История с другим контентом
export const CustomContent: Story = {
  render: (args) => <PopUpWithState {...args} />,
  args: {
    title: 'Другой контент',
    children: (
      <div>
        <h4>Вложенный заголовок</h4>
        <p>Можно вставлять любой JSX.</p>
        <Button variant="outline" onClick={() => alert('Клик внутри попапа!')}>
          Кнопка внутри
        </Button>
      </div>
    ),
  },
}; 