import React, { useEffect } from 'react';
import './PopUp.css'; // Импортируем стили

interface PopUpProps {
  isOpen: boolean;
  onClose: () => void;
  children: React.ReactNode;
  title?: string; // Необязательный заголовок
}

const PopUp: React.FC<PopUpProps> = ({ isOpen, onClose, children, title }) => {
  // Закрытие по клику на Esc
  useEffect(() => {
    const handleEscape = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        onClose();
      }
    };

    if (isOpen) {
      document.addEventListener('keydown', handleEscape);
    }

    // Очистка слушателя при размонтировании или закрытии
    return () => {
      document.removeEventListener('keydown', handleEscape);
    };
  }, [isOpen, onClose]);

  // Не рендерим ничего, если попап закрыт
  if (!isOpen) {
    return null;
  }

  // Обертка для обработки клика вне контента
  const handleOverlayClick = (event: React.MouseEvent<HTMLDivElement>) => {
    // Проверяем, что клик был именно по оверлею, а не по контенту внутри
    if (event.target === event.currentTarget) {
      onClose();
    }
  };

  return (
    <div className="popup-overlay" onClick={handleOverlayClick}>
      <div className="popup-content">
        {/* Кнопка закрытия */}
        <button className="popup-close-button" onClick={onClose}>
          &times; {/* Символ крестика */}
        </button>

        {/* Заголовок (если есть) */}
        {title && <h2 className="popup-title">{title}</h2>}

        {/* Контент попапа */}
        <div className="popup-body">
          {children}
        </div>
      </div>
    </div>
  );
};

export default PopUp; 